import React, { Component } from 'react';

class About extends Component {
	componentDidMount() {
			var About=document.getElementById("About"); //#About 
			var boardTopAbout=About.firstElementChild;
			var tabsAbout=boardTopAbout.lastElementChild;
			var tabsAboutUl=tabsAbout.lastElementChild;
			var tabsAboutLi=tabsAboutUl.children;
			var SliderAbout=boardTopAbout.nextElementSibling;
			var boxAbout=SliderAbout.firstElementChild;
			var slideListAbout=boxAbout.firstElementChild;
			var slideAbout=slideListAbout.children;
			var NavAbout=boxAbout.lastElementChild;
			var NavUlAbout=NavAbout.firstElementChild;
			var [prevAbout, nextAbout]=NavUlAbout.children;
			var Ab=0;
			var lengAb=slideAbout.length;

			for(var p=0; p<tabsAboutLi.length; p++){
				tabsAboutLi[p].idx=p;
				tabsAboutLi[p].addEventListener("click", function(e){
					e.preventDefault();
					var n=e.currentTarget.idx;
					for(var k=0; k<tabsAboutLi.length; k++){
						if(k == n){
							tabsAboutLi[k].classList.add("active");
							slideListAbout.style.left=1200*n*(-1)+"px";
						}
						else{
							tabsAboutLi[k].classList.remove("active");
						}
					}
				});
			}
			function moverightAbout() {
				if(Ab<(lengAb-1)){
					Ab=Ab+1;
				}
				else{
					Ab=0;
				}	
					for(var k=0; k<tabsAboutLi.length; k++){
						if(k == Ab){
							tabsAboutLi[k].classList.add("active");
						}
						else{
							tabsAboutLi[k].classList.remove("active");
						}
					}
				slideListAbout.style.left=1200*Ab*(-1)+"px";
			}
			setInterval(moverightAbout, 6000);
			nextAbout.addEventListener("click", function(e){
				e.preventDefault();
				moverightAbout();
			});
			prevAbout.addEventListener("click", function(e){
				e.preventDefault();
				if(Ab>0){
					Ab=Ab-1;
				}
				else{
					Ab=lengAb-1;
				}
					for(var k=0; k<tabsAboutLi.length; k++){
						if(k == Ab){
							tabsAboutLi[k].classList.add("active");
						}
						else{
							tabsAboutLi[k].classList.remove("active");
						}
					}
				slideListAbout.style.left=1200*-Ab+"px";
			});
	}
	render() {
		return (
			<React.Fragment>
				<div id="About">
					<div className="board_top">
						<div className="title">
							<h3>ABOUT ZIP</h3>
						</div>
						<div className="tabs">
							<ul>
								<li className="active"><a href="/">2020</a></li>
								<li><a href="/">2019</a></li>
								<li><a href="/">2016</a></li>
							</ul>
						</div>
					</div>
					<div className="slider">
						<div className="box">
							<div className="slide_list">
								<div className="slide active">
									<div className="picture">
										<img src="https://prontand.github.io/react_test2/assets/images/About1.jpeg" alt="about1" />
									</div>
									<div className="txtbox">
										<span className="line1">'유아인X박신혜' </span>
										<span className="line2">영화 ‘#살아있다’가 올여름 관객들을 사로잡을 취향 저격 관람 포인트를 공개했다.</span>
										<span className="line3">‘#살아있다’는 원인불명 증세의 사람들이 공격을 시작하며 통제 불능에 빠진 가운데, 데이터, 와이파이, 문자, 전화 모든 것이 끊긴 채 홀로 아파트에 고립된 이들의 이야기를 그린 생존 스릴러다. 관객들의 취향을 제대로 저격할 첫 번째 관람 포인트는 유아인과 박신혜의 매력적인 시너지이다. ‘#살아있다’를 통해 첫 연기 호흡을 맞춘 유아인과 박신혜는 유일한 생존자 ‘준우’와 ‘유빈’ 캐릭터를 완벽 소화,</span>
									</div>	
								</div>
								<div className="slide">
									<div className="picture">
										<img src="https://prontand.github.io/react_test2/assets/images/About2.jpeg" alt="about3" />
									</div>
									<div className="txtbox">
										<span className="line1">'가장 보통의 연애' 공효진</span>
										<span className="line2">[노컷 인터뷰] 영화 '가장 보통의 연애' 선영 역 공효진</span>
										<span className="line3">공효진은 빨간 마이크를 들고 앉아 있었다. 자기와 멀리 떨어져 있는 저 끝 기자에게도 목소리가 제대로 들릴까 싶었다며, 집에 있는 자기 마이크를 가져왔단다. 이게 뭐냐고 묻는 기자에게 "안 써보셨어요? 인생이 달라지는데"라고 한 공효진의 답에 인터뷰 시작부터 웃음이 터졌다. 싸이키 같은 조명이 나와서 더 흥이 난다는 노래방 마이크로 범상치 않게 등장한 공효진은, 어떤 말을 할지 자꾸 궁금해지는 흥미로운 인터뷰이였다. 지난달 27일 오후, 서울 종로구 소격동의 한 카페에서 영화 '가장 보통의 연애'(감독 김한결) 선영 역을 맡은 배우 공효진을 만났다. '썸 타는 남자 여자가 술 먹는 얘기에요'라는 설명을 듣고 무슨 얘기일지 궁금했고, 너무 재미있어서 합류하게 됐다는 공효진은 본인 표현에 따르면 "냉기만 남은 사람"인 선영 역을 제 옷처럼 입었다. 공효진은 이제 차차 쌀쌀해지는 날씨에 딱 어울리는 영화라며, 무엇보다 엔딩이 깔끔해서 마</span>
									</div>	
								</div>
								<div className="slide">
									<div className="picture">
										<img src="https://prontand.github.io/react_test2/assets/images/About3.gif" alt="about2" />
									</div>
									<div className="txtbox">
										<span className="line1">'입체적 연기' 김우빈 </span>
										<span className="line2">영화 '마스터'에서 김우빈(27)은 이병헌, 강동원 등 쟁쟁한 배우들 속에서도 전혀 밀리지 않는 존재감을 보여준다.</span>
										<span className="line3">그가 맡은 캐릭터가 영화 속에서 가장 입체적으로 그려진 덕분이다. 조 단위의 사기 사건을 둘러싸고 경찰과 사기꾼, 그리고 그의 브레인의 쫓고 쫓기는 추격전을 그린 '마스터'에서 김우빈은 진회장(이병헌)을 보좌하는 컴퓨터 프로그램 전문가 박장군 역을 맡았다. 박장군은 서민을 등쳐먹는 금융 피라미드 조직이 경찰에 일망타진될 위기에 처하자 지능범죄수사대 김재명(강동원)과 진회장 사이에서 아슬아슬한 줄타기를 하며 제 돈 찾을 궁리만 하는 인물이다. 입체적인 캐릭터를 더 현실감 있게 살린 것은 김우빈의 연기력이다. 이리 붙었다가 저리 붙었다 하며 갈등과 균열을 일으키지만, 이상하게 밉지 않은 악역이다. 능글맞지만 때로는 귀엽고, 제 꾀에 제가 넘어갈 때는 측은하게도 느껴지기도 한다.</span>
									</div>	
								</div>
							</div>
							<div className="navigation">
								<ul>
									<li className="prev"><a href="/"></a></li>
									<li className="next"><a href="/"></a></li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</React.Fragment>
		);
	}
}

export default About;
