import React, { Component } from 'react';

class MainSlider extends Component {
	componentDidMount() {
		var Slider=document.getElementsByClassName("main_slider")[0]; 
		var box=Slider.firstElementChild;
		var [sliderUl, controller]=box.children;
		var slide=sliderUl.children; 
		var [pauseBtn, pagination]=controller.children;
		var bullet=pagination.getElementsByTagName("li"); 
		var pagebtn=Slider.lastElementChild;
		var pagebtnUl=pagebtn.firstElementChild;
		var [next, prev]=pagebtnUl.children;
		var curindex=0;
		var leng=slide.length;

		for(var i=0; i<bullet.length; i++){
			bullet[i].idx=i;
			bullet[i].addEventListener("click", function(e){
				e.preventDefault();
				curindex=e.currentTarget.idx;
				for(var j=0; j<bullet.length; j++){
					if(j == curindex){ 
						bullet[j].classList.add("active");
						slide[j].classList.add("active");
					}
					else{
						bullet[j].classList.remove("active");
						slide[j].classList.remove("active");
					} 
				} 
			}); 
		}
		function moveRight(){
			if(curindex < (leng-1)){
					curindex=curindex+1;
				}
			else{
				curindex=0;
			}
			for(var i=0; i<leng; i++){
				if(i == curindex){
					slide[i].classList.add("active");
					bullet[i].classList.add("active");
				}	
				else{
					slide[i].classList.remove("active");
					bullet[i].classList.remove("active");
				}
			}
		}
		function moveLeft(){
			if(curindex > 0){
				curindex=curindex-1;
			}
			else{
				curindex=leng-1;
			}
			for(var i=0; i<leng; i++){
				if(i == curindex){
					slide[i].classList.add("active");
					bullet[i].classList.add("active");
				}	
				else{
					slide[i].classList.remove("active");
					bullet[i].classList.remove("active");
				}
			}
		}
		next.addEventListener("click", function(e){
			e.preventDefault();         
			moveRight();
		});
		prev.addEventListener("click", function(e){
			e.preventDefault();       
			moveLeft();
		});
		var mainTimer=setInterval(moveRight, 6000); 
		pauseBtn.addEventListener("click", function(e){
			e.preventDefault();
			if(pauseBtn.classList.contains("pause")){
				pauseBtn.classList.remove("pause");
				pauseBtn.classList.add("play");
				clearInterval(mainTimer);
			}
			else{
				pauseBtn.classList.remove('play');
				pauseBtn.classList.add('pause');
				mainTimer=setInterval(moveRight, 6000);
			}
		});
	}
	render() {
		return (
			<React.Fragment>
				<div className="main_slider">
					<div className="box">
						<ul>
							<li className="slide active">
								<div className="picture">
									<img src="https://prontand.github.io/react_test2/assets/images/main_picture1.jpeg" alt="main_picture1" />
								</div>
								<div className="bg_visual"><img src="https://prontand.github.io/react_test2/assets/images/bg_visual.jpg" alt="bg_main" /></div>
								<div className="text">
									<div className="inner">
										<span className="tit">#살아있다</span>
										<span className="cast">CAST:<br/>유아인 / YOO Ah-In<br/>박신혜 / PARK Shin-Hye</span>
										<span className="rel">RELEASE: 2020. 6. 24</span>
									</div>
								</div>
							</li>
							<li className="slide"><div className="picture">
									<img src="https://prontand.github.io/react_test2/assets/images/main_picture2.jpg" alt="main_picture2" />
								</div>
								<div className="bg_visual"><img src="https://prontand.github.io/react_test2/assets/images/bg_visual.jpg" alt="bg_main" /></div>
								<div className="text">
									<div className="inner">
										<span className="tit">#가장 보통의 연애</span>
										<span className="cast">CAST:<br/>김래원 / KIM Rae-Won<br/>공효진 / KONG Hyo-Jin<br/>강기영 / KANG Ki-Young<br/>정웅인 / JUNG Woong-In<br/>장소연 / JANG So-Yeon</span>
										<span className="rel">RELEASE: 2019. 10. 2</span>
									</div>
								</div>
							</li>
							<li className="slide"><div className="picture">
									<img src="https://prontand.github.io/react_test2/assets/images/main_picture3.jpg" alt="main_picture3" />
								</div>
								<div className="bg_visual"><img src="https://prontand.github.io/react_test2/assets/images/bg_visual.jpg" alt="bg_main" /></div>
								<div className="text">
									<div className="inner">
										<span className="tit">#국가부도의 날</span>
										<span className="cast">CAST:<br/>김혜수 / KIM Hye-Soo<br/>유아인 / YOO Ah-In<br/>허준호 / HU Joon-Ho<br/>조우진 / JO Woo-Jin<br/>그리고 뱅상 카셀 / Vincent CASSEL</span>
										<span className="rel">RELEASE: 2018. 11. 28</span>
									</div>
								</div>
							</li>
							<li className="slide"><div className="picture">
									<img src="https://prontand.github.io/react_test2/assets/images/main_picture4.jpeg" alt="main_picture4" />
								</div>
								<div className="bg_visual"><img src="https://prontand.github.io/react_test2/assets/images/bg_visual.jpg" alt="bg_main" /></div>
								<div className="text">
									<div className="inner">
										<span className="tit">#마스터</span>
										<span className="cast">CAST:<br/>이병헌 / LEE Byung-Hun<br/>강동원 / Gang Dong-Won<br/>김우빈 / KIM Woo-Bin</span>
										<span className="rel">RELEASE: 2016. 12. 21</span>
									</div>
								</div>
							</li>
							<li className="slide"><div className="picture">
									<img src="https://prontand.github.io/react_test2/assets/images/main_picture5.jpg" alt="main_picture5" />
								</div>
								<div className="bg_visual"><img src="https://prontand.github.io/react_test2/assets/images/bg_visual.jpg" alt="bg_main" /></div>
								<div className="text">
									<div className="inner">
										<span className="tit">#두근두근 내인생</span>
										<span className="cast">CAST:<br/>강동원 / Gang Dong-Won<br/>송혜교 / Song Hye-Kyo<br/>조성목 / Cho Seong-Mok</span>
										<span className="rel">RELEASE: 2014. 9. 3</span>
									</div>
								</div>
							</li>
						</ul>
						<div className="controller">
							<div className="pause-play pause">
								<a href="/"></a>
							</div>
							<div className="pagination">
								<ul>
									<li className="active"><a href="/"></a></li>
									<li><a href="/"></a></li>
									<li><a href="/"></a></li>
									<li><a href="/"></a></li>
									<li><a href="/"></a></li>
								</ul>
							</div>
						</div>
					</div>
					<div className="pagebtn">
						<ul>
							<li className="next"><a href="/">Next</a></li>
							<li className="prev"><a href="/">Prev</a></li>
						</ul>
					</div>
				</div>
			</React.Fragment>
		);
	}
}

export default MainSlider;
